<template>
  <div id="app">
    <div id="drop_zone" v-if="!isFileLoad" v-cloak @drop.prevent="addFile" @dragover.prevent>
      <img src="./assets/img/upload.svg" alt = "UPLOAD">
      <span>Put image here</span>
      <p>or</p>
      <input id = "img_input" type="file" name="file" v-on:change="addFileFromButton">
      <label for="img_input">Choose a file</label>
    </div>
    <ImProcessing v-if="isFileLoad" :image="image"/>
  </div>
</template>

<script>
import ImProcessing from "./components/ImProcessing";

export default {
  name: 'app',
  data: function(){
      return{
          image: undefined,
          bufferArray: undefined,
          isFileLoad: false
      }
  },
  components: {
    ImProcessing
  },
  methods:{
    addFile(e){
      let dropedFiles = e.dataTransfer.files;
      if(!dropedFiles) return;
      this.loadFileToImage(dropedFiles[0]);
    },
    addFileFromButton(e) {
      let file = e.target.files;
      if(!file) return;
      this.loadFileToImage(file[0]);
    },
    loadFileToImage(file){
      console.log(file);
      let reader = new FileReader();
      let app_pointer = this;
      reader.onload = function (e) {
        let img = new Image();
        img.onload = () => {
          console.log(img.width, img.height, img.decoding);
          app_pointer.image = img;
          app_pointer.isFileLoad = true;
        }
        img.src = e.target.result;
      };
      reader.readAsDataURL(file);
    }
  }
}
</script>

