import * as tf from "@tensorflow/tfjs";

export async function loadModelAndPredict(metricName, values_noisy, values_filtered){
    let loaded_model = await tf.loadLayersModel("https://viktorovich21.github.io/"+ metricName +"/model.json");
    let predicted_val = loaded_model.predict(tf.tensor(values_noisy, [1, 16])).dataSync();
    let retVal = [denormalize(predicted_val[0], metricName), predicted_val[0], predicted_val[0]];
    predicted_val = loaded_model.predict(tf.tensor(values_filtered, [1, 16])).dataSync();
    retVal[1] = denormalize(predicted_val[0], metricName);
    retVal[2] = retVal[1] - retVal[0];
    return retVal;
}

function denormalize(value, metric) {
    let max = 0.0;
    let min = 0.0;

    switch (metric)
    {
        case "ADDSSIM":
            max = 0.9999;
            min = 0.9783;
            break;
        case "GMSD":
            max = 0.2702;
            min = 0.0024;
            break;
        case "GSM":
            max = 0.9998;
            min = 0.9437;
            break;
        case "HaarPSI":
            max = 0.9870;
            min = 0.2237;
            break;
        case "IWSSIM":
            max = 0.9994;
            min = 0.9437;
            break;
        case "MSSSIM":
            max = 0.9995;
            min = 0.5354;
            break;
        case "PSNR":
            max = 38.8070;
            min = 18.5933;
            break;
        case "PSNRHVSM":
            max = 51.7829;
            min = 20.6627;
            break;
        case "WSNR":
            max = 50.0256;
            min = 20.6656;
            break;
        default:
            max = 1;
            min = -1;
    }

    if(max < value) return max;
    else if(min > value) return min;
    else return (((value - (-1)) * (max - min))/(1 - (-1))) + min;
}

export default loadModelAndPredict;